@import '../../utils/variables';
@import '../../utils/mixins';

.main {
    height: calc(100% - 6.3rem + 2.6rem);
    max-height: calc(58rem - 6.3rem - 2.6rem);
    position: relative;
    display: flex;
    flex-direction: column;
    background: $bg--dark-gray;

    @include onDesktop {
      max-height: calc(91.5rem - 6.3rem - 2.6rem);
    }
}

.content {
  position: relative;
  top: -2.6rem;
  padding: 0 1.6rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.title {
  font-family: 'Jost';
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 2.6rem;
  text-align: center;
  letter-spacing: 0.03em;
  text-transform: uppercase;

  color: #FFFFFF;
}

.comments {
  position: relative;
  top: -2.6rem;
}

